//2分探索もどきで探索
function search(arr, len, n) {
  if (n == 0) return 0;
  var l = 0;
  var r = len - 1;

  while (l <= r) {
    var m = ~~((l + r) * 0.5);

    if (arr[m] < n) {
      l = m + 1;
    } else {
      r = m - 1;
    }
  }

  return r;
}

function CubicBezier(cp1x, cp1y, cp2x, cp2y) {
  var p1y = arguments.length > 4 && arguments[4] !== undefined ? arguments[4] : 0;
  var p2y = arguments.length > 5 && arguments[5] !== undefined ? arguments[5] : 1;
  var res = arguments.length > 6 && arguments[6] !== undefined ? arguments[6] : 50;
  var xList = [];
  var tList = [];
  cp1y = cp1y;
  cp2y = cp2y;

  for (var i = 0; i <= res; i++) {
    var t = i / res;
    xList.push(bezier(t, 0, cp1x, cp2x, 1));
    tList.push(t);
  }

  return function (x) {
    x = x < 0 ? 0 : x > 1 ? 1 : x;
    var i = search(xList, res, x);
    var ax = xList[i];
    var bx = xList[i + 1];
    var at = tList[i];
    var bt = tList[i + 1];
    return bezier( //tを計算
    (x - ax) / (bx - ax) * (bt - at) + at, //yを求める
    p1y, cp1y, cp2y, p2y);
  };
} // maximaで求めたらこうなったベジェ関数。

function bezier(t, p0, p1, p2, p3) {
  var mt = 1 - t;
  return p3 * t * t * t + 3 * mt * p2 * t * t + 3 * mt * mt * p1 * t + mt * mt * mt * p0;
}

export default CubicBezier;
